.header{
    display: flex;
    justify-content: space-between;
    padding-top: 40px;
}
.header-menu{
    display: flex;
    list-style-type: none;
    align-items: center;
    font-size: 24px;
}
.header-menu-item:not(:last-child){
    margin-right: 40px;
}
.header-btn{
    position: relative;
    padding: 20px 30px;
    font-size: 20px;
    text-transform: uppercase;
    font-weight: 500;
    background-color: var(--bg-color);
    border: none;
}
.header-btn::before{
    content: "â€”";
    position: absolute;


